* {
  outline: 1px solid blue;
}
// banner
.banner {
  height: 840px;
  background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/banner.png?raw=true");
  background-size: cover;
  background-position: center center;
  position: relative;
  @include pad {
    background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/banner-sm.png?raw=true");
  }
  @include mobile {
    height: 394px;
  }
  &-slogan {
    color: $text-color-heavy;
    text-align: right;
    position: absolute;
    top: 348px;
    right: 20px;
    @include pad {
      top: 372px;
      right: 35px;
      .banner-intro {
        font-size: $fz-base * 1.25;
      }
      .banner-text {
        font-size: $fz-base * 3;
      }
    }
    @include mobile {
      top: 164px;
      right: 15px;
      .banner-intro {
        font-size: $fz-base * 1;
        line-height: $fz-base * 1.25;
      }
      .banner-text {
        font-size: $fz-base * 2;
        line-height: $fz-base * 4.5;
      }
    }
  }
}

//h2
.index-subtitle {
  font-size: $fz-base * 3;
  font-weight: 700;
  margin-bottom: $spacing * 2.5;
  color: $text-color-heavy;
  text-align: center;
  @include pad {
    font-size: $fz-base * 2;
    margin-bottom: $spacing * 2;
  }
  @include mobile {
    font-size: $fz-base * 1.5;
    line-height: $spacing * 3;
  }
}

//about
.about {
  padding-top: $spacing * 3.75;
  padding-bottom: $spacing * 5;
  background-color: $background-light;
  @include pad {
    padding-top: $spacing * 2.5;
    padding-bottom: $spacing * 3.5;
  }
  @include mobile {
    padding-top: $spacing * 2;
    padding-bottom: $spacing * 2.5;
  }
  &-body {
    display: flex;
    justify-content: space-between;
    max-width: 100%;
    @include pad {
      flex-wrap: wrap;
      justify-content: space-around;
    }
  }
  &-items1,
  &-items2,
  &-items3,
  &-items4 {
    max-width: 23%;
    margin: 0 auto;
  }

  &-items1,
  &-items2 {
    @include pad {
      max-width: 255px;
      margin-left: $spacing * 3.35;
      margin-right: $spacing * 3.35;
      margin-bottom: $spacing * 2;
    }
  }
  &-items3,
  &-items4 {
    @include pad {
      max-width: 255px;
      margin-left: $spacing * 3.35;
      margin-right: $spacing * 3.35;
    }
  }
  &-items1,
  &-items2,
  &-items3 {
    @include mobile {
      max-width: 255px;
      margin-left: $spacing * 3.8;
      margin-right: $spacing * 3.8;
      margin-bottom: $spacing * 2;
    }
  }
  &-icon {
    width: 100px;
    padding: 8.08px;
    height: 100px;
    margin: 0 auto;
    margin-bottom: $spacing;
  }
  &-title {
    font-size: $fz-base * 1.5;
    color: $text-color-heavy;
    text-align: center;
    margin-bottom: $spacing * 0.5;
  }
}

//classic glass
.classic-glass {
  padding-top: $spacing * 3.75;
  padding-bottom: $spacing * 5;
  @include pad {
    padding-top: $spacing * 2.5;
    padding-bottom: $spacing * 3.5;
  }
  @include mobile {
    padding-top: $spacing * 2;
    padding-bottom: $spacing * 2.5;
    max-width: 100%;
    margin: 0 auto;
  }
  &-body1,
  &-body2,
  &-body3 {
    display: flex;
    align-items: center;
    color: $text-color-bright;
    max-width: 100%;
    margin: 0 auto;
    position: relative;
  }
  &-body1,
  &-body2 {
    margin-bottom: $spacing * 2;
    @include pad {
      margin-bottom: $spacing * 0.5;
    }
  }
  &-items1,
  &-items2,
  &-items3 {
    display: flex;
    align-items: center;
    max-width: 100%;
    height: auto;
    @include mobile {
      position: absolute;
    }
  }
  &-img,
  &-img2 {
    max-width: 58%;
    height: auto;
    object-fit: cover;
    @include pad {
      max-width: 52%;
    }
    @include mobile {
      max-width: 100%;
      // position: relative;
    }
  }
  &-img2 {
    order: (2);
    @include pad {
      order: (-2);
    }
  }
  .material-icons {
    transform: scale(2);
    margin-left: $spacing * 2.8125;
    margin-right: $spacing * 1.5;
    @include pad {
      transform: scale(1.67);
      margin-left: $spacing * 2.45;
      margin-right: $spacing * 1;
    }
    // @include mobile {
    //     margin-left: $spacing*0;
    //     margin-right: $spacing*0;
    //     position: absolute;
    //     left: 30%;
    // }
  }
  &-icon3 {
    width: 48px;
    height: 48px;
    margin-left: $spacing * 1.875;
    margin-right: $spacing * 0.5;
    @include pad {
      width: 40px;
      height: 40px;
      margin-left: $spacing * 1.875;
      margin-right: $spacing * 0.5;
    }
    // @include mobile {
    //     margin-left: $spacing*0;
    //     margin-right: $spacing*0;
    //     position: absolute;
    //     left: 20%;
    // }
  }
  &-icon2 {
    @include pad {
      width: 40px;
      height: 40px;
      margin-left: $spacing * 1.875;
      margin-right: $spacing * 0.5;
    }
    // @include mobile {
    //     margin-left: $spacing*0;
    //     margin-right: $spacing*0;
    //     position: absolute;
    //     left: 20%;
    // }
  }

  &-title1,
  &-title2,
  &-title3 {
    font-size: $fz-base * 3;
    font-style: italic;
    font-weight: 900;
    @include pad {
      font-size: $fz-base * 1.75;
      line-height: $spacing * 4;
    }
  }
  &-title2 {
    margin-left: $spacing * 0.625;
    margin-right: $spacing * 1.875;
    @include pad {
      margin-left: $spacing * 0;
      margin-right: $spacing * 0;
    }
  }
  // &-title1{
  //     @include mobile {
  //         position: absolute;
  //         left: 40%;
  //     }
  // }
  // &-title2, &-title3 {
  //     @include mobile {
  //         position: absolute;
  //         left: 33%;
  //     }
  // }
}

//co-branded-glass 
.cobranded-glass {
    background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/bg.png?raw=true");
    background-size: cover;
    background-position: center center;
    padding-top: $spacing*3.75;
    padding-bottom: $spacing*5;
    &-body {
        display: flex;
    }
}
